import React from 'react'

const ListTweets = (props) => {

    // Error message can show here

    const tweets = props.tweets
    if(tweets.length === 0)
    {
        return (
        <div>
            <h3>Welcome to this sorting tool!</h3>
            <p>Type in a twitter username (the one with the '@' before it) to get an account's most recent tweets, up to 3,200.</p>
            <p>You can sort their tweets by Date, Likes, or Retweets, and choose either ascending or descending order.</p>
            <p>A loading symbol will show while the tweets are being fetched.</p>
            <p>If this area is empty (not even this welcome message) it means the tweets are being rendered :)</p>
            <p>If you enjoy this little tool, follow me <a href="https://twitter.com/averywlittle" target="_blank">@averywlittle</a></p>
        </div>
        )
    } else {
        return (
            <div>
                <p>{tweets.length} loaded!</p>
                <br></br>
            </div>
        )
    }
}

export default ListTweets